// 路由配置
import Vue from 'vue'
import Router from 'vue-router'

import NotDefined from '@/components/404/404'
import Home from '@/components/Home'
import Page from '@/components/Page'
import Setting from '@/components/Setting'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      redirect: '/home'
    },
    {
      path: '/home',
      name: 'Home',
      component: Home
    },
    {
      path: '/page',
      name: 'Page',
      component: Page
    },
    {
      path: '/setting',
      name: 'Setting',
      component: Setting
    },
    {
      path: '*',
      name: 'NotDefined',
      component: NotDefined
    }
  ]
})



