<template>
    <div class="home-cantainer">
        <el-card class="box-card" v-for="(items,i) in arrListG" :key="i" :style="'background:'+color.bgcolor">
            <div slot="header" class="clearfix" :id="i">
                <i class="iconfont icon-clip icon-size"></i>
                <span :style="'color:'+color.textcolor" title="类名">{{items.classtit}}</span>
                <el-button style="float: right; padding: 3px 0" type="text">
                    <!-- <span class="iconfont icon-ico_compile" @click="updateList(i)" title="点击修改类名"></span> -->
                </el-button>
            </div>

            <div class="list-conbox">
                <el-row :gutter="30">
                    <el-col :xs="24" :sm="12" :md="8" :lg="6" :xl="6" 
                    v-for="(dataItem,j) in items.data" :key="j">
                        <div class="grid-content bg-purple-light">
                            <a :href="dataItem.url" target="_blank" :title="dataItem.info" class="card-box">
                                <img v-if="dataItem.imageUrl" :src="dataItem.imageUrl" alt="未加载" class="tit">
                                <img v-if="!dataItem.imageUrl" src="@/assets/001.jpg" alt="未加载" class="tit">
                                <div>
                                    <p :style="'color:'+color.textcolor">{{dataItem.title}}</p>
                                    <p :style="'color:'+color.textcolor">{{dataItem.info}}</p>
                                </div>
                            </a>
                        </div>
                    </el-col>
                </el-row>
            </div>
            <br>
            <br>
        </el-card>
    </div>
</template>

<script>
export default {
    props:['color','arrListG'],
    data() {
        return {
            
        }
    },
    methods: {
        
    },
    created(){

    }
}
</script>

<style scoped>
    /* 分组卡片 */
    .box-card{
        min-width: 200px;
        margin-top: 30px;
        margin-bottom: 30px;
    }

    /* 列表卡片 */
    .el-col {
        border-radius: 4px;
        min-width: 200px;
    }
    .bg-purple-dark {
        background: #eee;
    }
    .bg-purple {
        background: #eee;
    }
    .bg-purple-light {
        background: #eee;
    }
    .grid-content {
        border-radius: 4px;
        min-height: 100px;
        margin-top: 30px;
        border:1px solid #ebeef5;
        box-shadow: 0 2px 12px 0 rgba(0,0,0,.1);
        display: flex;
        justify-content: center;
        align-items: center;
        cursor: pointer;
        padding: 10px 10px;
        box-sizing: border-box;
        position: relative;
        overflow: hidden;
    }

    /* 卡片内容 */
    .card-box{
        width: 100%;
        height: 100%;
        display: flex;
        justify-content:space-around;
        align-items: center;
        text-decoration: none;
    }
    .card-box>.tit{
        width: 60px;
        height: 60px;
    }
    .card-box>div{
        display: flex;
        flex-direction: column;
        justify-content: center;
        width: 75%;
        text-align: left;
        margin-left: 20px;
    }
    .card-box>div>p:first-child{
        font-weight: bold;
        font-size: 14px;
        margin: 0;
    }
    .card-box>div>p:last-child{
        color: #555;
        font-size: 12px;
        margin: 0;
        margin-top: 10px;
    }
</style>



