<template>
    <div class="set-cantainer">
        <el-card class="box-card">
            <h1>setting</h1>
            <div class="set-color">
                <el-color-picker v-model="color.bgcolor" size="medium"></el-color-picker>
                <el-color-picker v-model="color.textcolor" size="medium"></el-color-picker>
                <el-color-picker v-model="color.activecolor" size="medium"></el-color-picker>
			</div>
            <el-button type="primary" icon="el-icon-refresh" circle @click="handelColor" size="small"></el-button>
        </el-card>
    </div>
</template>

<script>
export default {
    data(){
        return{
            color:{
				bgcolor:'#fff',
				textcolor:'#000',
				activecolor:'#6BAAFF',
			},
        }
    },
    methods:{
        handelColor(){
            this.$emit("listenToChildEvent",this.color)
        }
    }
}
</script>

<style scoped>
    .set-cantainer{
        width: 100%;
    }

    .text {
        font-size: 14px;
    }

    .item {
        padding: 18px 0;
    }

    .box-card{
        height: 98vh;
        /* margin-top: 10px; */
    }
</style>



