<template>
    <div class="page-cantainer">
        <el-card class="box-card" v-for="(items,i) in arrList" :key="i" :style="'background:'+color.bgcolor">
            <div slot="header" class="clearfix" :id="i">
                <i class="iconfont icon-clip icon-size"></i>
                <span :style="'color:'+color.textcolor" title="类名">{{items.classtit}}</span>
                <el-button style="float: right; padding: 3px 0" type="text">
                    <span class="iconfont icon-ico_compile" @click="updateList(i)" title="点击修改类名"></span>

                    <span class="iconfont icon-shanchu delect-list" @click="deleteList(i)" title="点击删除类"></span>
                </el-button>
            </div>

            <div class="list-conbox">
                <el-row :gutter="30">
                    <el-col :xs="24" :sm="12" :md="8" :lg="6" :xl="6" 
                    v-for="(dataItem,j) in items.data" :key="j">
                        <div class="grid-content bg-purple-light">
                            <a :href="dataItem.url" target="_blank" :title="dataItem.info" class="card-box">
                                <img v-if="dataItem.imageUrl" :src="dataItem.imageUrl" alt="未加载" class="tit">
                                <img v-if="!dataItem.imageUrl" src="@/assets/001.jpg" alt="未加载" class="tit">
                                <div>
                                    <p :style="'color:'+color.textcolor">{{dataItem.title}}</p>
                                    <p :style="'color:'+color.textcolor">{{dataItem.info}}</p>
                                </div>
                            </a>
                            <span class="card-updata-box"></span>
                            <span class="iconfont icon-ico_compile card-updata" @click="updateCardBtn(i,j)"></span>
                        </div>
                    </el-col>

                    <el-col :xs="24" :sm="12" :md="8" :lg="6" :xl="6" title="点击添加卡片">
                        <div class="grid-content bg-purple-light" @click="addCardBtn(1,i)">
                            <i class="el-icon-plus"></i>
                        </div>
                    </el-col>
                </el-row>
            </div>
            <br>
            <br>
        </el-card>

        <el-dialog title="新增卡片" :visible.sync="dialogFormVisible">
            <el-form :model="form">
                <el-form-item label="网址" label-width="50px">
                    <el-input v-model="form.url" placeholder="https://www.xxx.com  http://www.xxx.com" autocomplete="off"></el-input>
                </el-form-item>
                <el-form-item label="标题" label-width="50px">
                    <el-input v-model="form.title" placeholder="VUE / REACT" autocomplete="off"></el-input>
                </el-form-item>
                <el-form-item label="介绍" label-width="50px">
                    <el-input v-model="form.info" placeholder="VUE是一个很好用的前端框架 / REACT是前端主流框架" autocomplete="off"></el-input>
                </el-form-item>
                <el-form-item label="图标" label-width="50px">
                    <el-upload action="https://jsonplaceholder.typicode.com/posts/"
                    list-type="picture-card" :on-preview="handlePictureCardPreview"
                    :on-remove="handleRemove" :on-success="handleSuccess" :limit="1" :on-exceed="handleLimit">
                        <i class="el-icon-plus"></i>
                    </el-upload>
                    <el-dialog :visible.sync="dialogVisible">
                        <img width="100%" :src="form.imageUrl" alt="">
                    </el-dialog>
                </el-form-item>
            </el-form>
            <div slot="footer" class="dialog-footer">
                <el-button @click="addCardBtn(2)">取 消</el-button>
                <el-button type="primary" @click="addCard(listIndex)">确 定</el-button>
            </div>
        </el-dialog>

        <el-dialog title="修改卡片" :visible.sync="updateDialogFormVisible">
            <el-form :model="cardForm">
                <el-form-item label="网址" label-width="50px">
                    <el-input v-model="cardForm.url" placeholder="https://www.xxx.com  http://www.xxx.com" autocomplete="off"></el-input>
                </el-form-item>
                <el-form-item label="标题" label-width="50px">
                    <el-input v-model="cardForm.title" placeholder="VUE / REACT" autocomplete="off"></el-input>
                </el-form-item>
                <el-form-item label="介绍" label-width="50px">
                    <el-input v-model="cardForm.info" placeholder="VUE是一个很好用的前端框架 / REACT是前端主流框架" autocomplete="off"></el-input>
                </el-form-item>
                <el-form-item label="图标" label-width="50px">
                    <el-upload action="https://jsonplaceholder.typicode.com/posts/"
                    list-type="picture-card" :on-preview="handlePictureCardPreview"
                    :on-remove="handleRemove" :on-success="handleSuccess" :limit="1" :on-exceed="handleLimit">
                        <i class="el-icon-plus"></i>
                    </el-upload>
                    <el-dialog :visible.sync="dialogVisible">
                        <img width="100%" :src="cardForm.imageUrl" alt="">
                    </el-dialog>
                </el-form-item>
            </el-form>
            <div slot="footer" class="dialog-footer">
                <el-button @click="updateCard(3)">取 消</el-button>
                <el-button type="danger" @click="updateCard(2)">删 除</el-button>
                <el-button type="primary" @click="updateCard(1)">确 定</el-button>
            </div>
        </el-dialog>
    </div>
</template>

<script>
export default {
    props:['color','arrList'],
    data(){
        return{
            dialogFormVisible: false,
            updateDialogFormVisible: false,
            form: {
                title: '',
                url:'',
                info:'',
                imageUrl: ''
            },
            listIndex:0,
            dialogVisible: false,
            cardForm:{},
            cardIndex:{
                i:'',
                j:''
            },
        }
    },
    methods:{
        // 修改类名
        updateList(i){ 
            this.$prompt('请输入类名', '类名修改', {
                confirmButtonText: '确定',
                cancelButtonText: '取消',
                inputPattern: /^(?!_)(?!.*?_$)[a-zA-Z0-9_\u4e00-\u9fa5]{4,12}$/,
                inputErrorMessage: '类名格式不正确'
            }).then(({ value }) => {
                let obj = this.arrList[i]
                obj.classtit = value
                this.arrList.splice(i, 1, obj)
                this.$notify({
                    title: '修改成功',
                    message: '您修改的类名是: ' + value,
                    type: 'success',
                    duration: 1500
                });
            }).catch(() => {
                this.$notify.info({
                    title: '消息',
                    message: '您已取消修改',
                    duration: 1500
                });   
            });
        },
        // 删除类
        deleteList(i){
            let val = this.arrList[i].classtit
            this.$confirm('此操作将永久删除<'+ val +'>类及该类别下所有卡片, 是否继续?', '提示', {
                confirmButtonText: '确定',
                cancelButtonText: '取消',
                type: 'warning'
            }).then(() => {
                this.arrList.splice(i, 1)
                this.$notify({
                    title: '删除成功',
                    message: "您已成功删除<"+ val +">类",
                    type: 'success',
                    duration: 1500
                });
            }).catch(() => {
                this.$notify.info({
                    title: '消息',
                    message: '您已取消删除',
                    duration: 1500
                });          
            });
        },
        // 添加卡片项按钮
        addCardBtn(n,i){ 
            if(n==1){this.dialogFormVisible = true}else{this.dialogFormVisible = false}
            console.log(i)
            this.listIndex = i
        },
        // 添加卡片项
        addCard(i){ 
            console.log(i)
            console.log(this.form)
            this.dialogFormVisible = false
            this.arrList[i].data.push(this.form)
            this.form = {title: '',url:'',info:'',imageUrl: ''}
        },
        // 删除图片上传
        handleRemove(file, fileList) {
            console.log(file, fileList);
        },
        // 图片上传成功
        handleSuccess(res, file, fileList){
            this.form.imageUrl = file.url;
            console.log(res)
            console.log(file)
            console.log(fileList)
        },
        // 查看上传图片
        handlePictureCardPreview(file) {
            this.form.imageUrl = file.url;
            this.dialogVisible = true;
        },
        // 图片上传超载
        handleLimit(files, fileList){
            this.$message.error('上传头像图片只能为一张,请删除后再次上传!');
        },
        //将图片压缩转成base64 方法
		getBase64Image(img) { 
			var canvas = document.createElement("canvas");
			var width = img.width;
			var height = img.height;
			// calculate the width and height, constraining the proportions 
			if(width > height) {
				if(width > 100) {
					height = Math.round(height *= 100 / width);
					width = 100;
				}
			} else {
				if(height > 100) {
					width = Math.round(width *= 100 / height);
					height = 100;
				}
			}
			canvas.width = width; /*设置新的图片的宽度*/
			canvas.height = height; /*设置新的图片的长度*/
			var ctx = canvas.getContext("2d");
			ctx.drawImage(img, 0, 0, width, height); /*绘图*/
			var dataURL = canvas.toDataURL("image/png", 0.8);
			return dataURL.replace("data:image/png;base64,", "");
        },
        // 修改卡片项按钮
        updateCardBtn(i,j){
            this.cardIndex.i = i
            this.cardIndex.j = j
            this.updateDialogFormVisible = true
            this.cardForm = this.arrList[i].data[j]
        },
        // 修改卡片项
        updateCard(n){
            if(n==1){
                this.arrList[this.cardIndex.i].data[this.cardIndex.j] = this.cardForm
                this.updateDialogFormVisible = false
                this.$notify({
                    title: '修改成功',
                    message: "您已成功修改该卡片",
                    type: 'success',
                    duration: 1500
                });
            }else if(n==2){
                this.$confirm('此操作将永久删除该卡片, 是否继续?', '提示', {
                    confirmButtonText: '确定',
                    cancelButtonText: '取消',
                    type: 'warning'
                }).then(() => {
                    this.arrList[this.cardIndex.i].data.splice(this.cardIndex.j, 1)
                    this.updateDialogFormVisible = false
                    this.$notify({
                        title: '删除成功',
                        message: "您已成功删除该卡片",
                        type: 'success',
                        duration: 1500
                    });
                }).catch(() => {
                    this.$notify.info({
                        title: '消息',
                        message: '您已取消删除',
                        duration: 1500
                    });          
                });
            }else{
                this.updateDialogFormVisible = false
                this.$notify.info({
                    title: '消息',
                    message: '您已取消修改',
                    duration: 1500
                });
            }
        }
    },
    created(){
        console.log(this.arrList)
    }
}
</script>


<style scoped>

    /* 分组卡片 */
    .box-card{
        min-width: 200px;
        margin-top: 30px;
        margin-bottom: 30px;
    }
    .delect-list{
        font-weight: bold;
        margin-left: 5px;
    }

    /* 列表卡片 */
    .el-col {
        border-radius: 4px;
        min-width: 200px;
    }
    .bg-purple-dark {
        background: #eee;
    }
    .bg-purple {
        background: #eee;
    }
    .bg-purple-light {
        background: #eee;
    }
    .grid-content {
        border-radius: 4px;
        min-height: 100px;
        margin-top: 30px;
        border:1px solid #ebeef5;
        box-shadow: 0 2px 12px 0 rgba(0,0,0,.1);
        display: flex;
        justify-content: center;
        align-items: center;
        cursor: pointer;
        padding: 10px 10px;
        box-sizing: border-box;
        position: relative;
        overflow: hidden;
    }

    /* 卡片内容 */
    .card-box{
        width: 100%;
        height: 100%;
        display: flex;
        justify-content:space-around;
        align-items: center;
        text-decoration: none;
    }
    .card-box>.tit{
        width: 60px;
        height: 60px;
    }
    .card-box>div{
        display: flex;
        flex-direction: column;
        justify-content: center;
        width: 75%;
        text-align: left;
        margin-left: 20px;
    }
    .card-box>div>p:first-child{
        font-weight: bold;
        font-size: 14px;
        margin: 0;
    }
    .card-box>div>p:last-child{
        color: #555;
        font-size: 12px;
        margin: 0;
        margin-top: 10px;
    }

    .card-updata-box{
        position: absolute;
        top: 0;right: 0;
        z-index: 99;
        width: 100px;height: 100px;
        background-color: #409EFF;
        transform: rotateZ(30deg) translate(0px,-90px);
    }
    .card-updata{
        position: absolute;
        top: 5px;right: 5px;
        z-index: 100;
        font-size: 15px;
        color: #EEEEEE;
    }
</style>



